                          {{ $t("Target") }}
                        </span>
                        <b-form-textarea
                          v-model="existitem1.target"
                          class="m-0 mt-1 saj-text"
                          disabled
                        />
                      </div>
                      <div class="m-0 mb-1 saj-title">
                        <span
                          style="
                            line-height: 24px;
                            letter-spacing: 0.4px;
                            color: #000000;
                          "
