                  ml-2
                  mb-1
                  saj-title
                "
                style="

                  line-height: 24px;
                  letter-spacing: 0.1em;
                  color: #000000;
                  width: 100%;
                "
              >
                <div
                  v-if="existitem1.file === null"
                  class="row align-items-center"
