                >
                  <feather-icon
                    class="mr-1"
                    icon="PaperclipIcon"
                    size="16"
                  />
                  {{ $t("No attachment") }}
                </div>
                <div
                  v-if="existitem1.file !== null"
                  class="row align-items-center"
                  style="font-size: 12px"
                >
                  <feather-icon
                    class="mr-1"
