                    {{ $t(existitem1.file.name) }}
                  </a>
                </div>
              </b-row>
              <div
                class="d-flex justify-content-start saj-title"
                style="
                  line-height: 24px;
                  letter-spacing: 0.1em;
                  color: #000000;
                "
              >
                {{ $t("Self Score") }}: {{ existitem1.self_score }}/6
              </div>
              <b-row class="mb-2">
                <b-col
                  cols="8"
                  class="d-flex align-items-center saj-title"
                  style="
                    line-height: 24px;
                    letter-spacing: 0.1em;
